body
{
  background-color: black;
}

/* Navbar */
nav 
{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  /* position: fixed; */

  color: white;
}


.l{
  background-color: #000;
}
.nav-links{
  width: 60%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

.nav-li{
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-size: 25px;
  font-style: normal; font-weight: 700;
}

@media screen and (max-width: 800px) {
 
  nav{
    justify-content: space-between;
    background-color: rgb(4 4 4 / 95%);
  }

  .nav-links
  {
    width: 85%;
  }

  .AboutPhotograph
  {
  padding-left: 9%;
  }

}
/* Navbar Ende*/

.HeaderVideo{
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
}

.kyvWZW {
  max-width: 1000px;
  margin: 0px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  -webkit-box-align: center;
  align-items: center;
  gap: 16px;
  padding: 0px 50px;
}

.hBWCUB {
  background: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  -webkit-box-pack: start;
  justify-content: flex-start;
  -webkit-box-align: center;
  align-items: center;
  border-radius: 10px;
  max-height: 340px;
  max-width: 300px;
  padding: 30px;
  box-shadow: rgb(0 0 0 / 20%) 0px 1px 3px;
  transition: all 0.2s ease-in-out 0s;
  color: black;
  text-decoration: none;
}